.project-container {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  height: 100%;
  width: 100%;
  background-color: #f5f9f6;

  .project-head {
    position: relative;
    display: flex;
    height: 300px;
    width: 100%;
    background-color: #164e98;
    justify-content: center;
    align-items: center;
    color: #f5f9f6;
    font-size: 64px;
    font-weight: bold;
  }

  .project-body {
    position: relative;
    display: flex;
    flex-direction: row;
    height: calc(100% - 300px);
    width: 100%;

    .image-box {
      // display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      position: relative;
      // height: 100%;
      width: 450px;

      .images {
        margin: 10px;
        width: 400px;
        // height: 300px;
        background-size: cover;
        background-position: center;
        border-radius: 10px;
        border: 2px solid black;
        cursor: pointer;
      }
    }

    .summary-box {
      position: relative;
      width: calc(100% - 500px);
      // height: 100%;
      padding-top: 15px;
      display: flex;
      flex-direction: column;
      align-items: center;
      // justify-content: center;
      font-size: 30px;
      color: #164e98;

      div {
        margin-bottom: 15px;
      }

      .project-about {
        // height: 250px;
        width: 95%;
      }

      .project-subject {
        margin-top: 25px;
        font-weight: bold;
      }

      .links-box {
        height: 50px;

        a {
          margin: 10px;

          i {
            color: #164e98;
            font-size: 48px;
          }
        }
      }
    }
  }

  @media (max-width: 680px) {
    height: auto;

    .project-head {
      font-size: 36px;
      height: 200px;
    }

    .project-body {
      flex-direction: column-reverse;
      height: auto;
      align-items: center;

      .image-box {
        margin-top: 10px;
        height: auto;
        width: 95%;

        .images {
          display: flex;
          margin: 10px 0;
          width: 100%;
          height: 200px;
        }
      }

      .summary-box {
        margin-top: 10px;
        width: 90%;
        height: auto;
        font-size: 20px;

        .project-about {
          display: flex;
          height: auto;
        }

        .project-subject {
          margin-top: 15px;
          margin-bottom: 5px;
        }
      }
    }
  }
}
